.program-highlights {
  background-color: #f8fafc;
  padding: 6rem 0;
}

.program-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

.program-text { max-width: 500px; }
.program-text .program-cta { display: flex; }

.program-icon {
  color: #8B5CF6;
  margin-bottom: 1rem;
}

.program-title {
  font-size: 2.5rem;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 2rem;
  line-height: 1.2;
}

.highlights-list {
  list-style: none;
  margin: 0 0 2.5rem 0;
  padding: 0;
}

.highlight-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  padding: 0.75rem 0;
}

.highlight-icon {
  color: #8B5CF6;
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.highlight-text { color: #374151; font-weight: 500; font-size: 1.125rem; }

.program-cta {
  margin-top: 2rem;
}

.program-image { display: flex; justify-content: center; align-items: center; }

.program-image-placeholder {
  width: 400px;
  height: 400px;
  background: linear-gradient(135deg, #8B5CF6, #7C3AED);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.program-image-placeholder::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><circle cx="20" cy="20" r="2" fill="white" opacity="0.1"/><circle cx="80" cy="40" r="1.5" fill="white" opacity="0.1"/><circle cx="40" cy="80" r="1" fill="white" opacity="0.1"/><circle cx="70" cy="70" r="2.5" fill="white" opacity="0.1"/><circle cx="30" cy="50" r="1" fill="white" opacity="0.1"/></svg>');
  animation: float 8s ease-in-out infinite;
}

.stretching-icon {
  color: white;
  z-index: 2;
  animation: pulse 3s ease-in-out infinite;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-10px) rotate(5deg);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 0.9;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .program-content { grid-template-columns: 1fr; gap: 3rem; text-align: center; }
  .program-text { max-width: none; margin-left: auto; margin-right: auto; }
  .program-cta { justify-content: center; }
  
  .program-title {
    font-size: 2.25rem;
  }
  
  .program-image-placeholder {
    width: 350px;
    height: 350px;
  }
}

@media (max-width: 768px) {
  .program-highlights { padding: 4rem 0; }
  .program-content { gap: 2.5rem; }
  .program-title { font-size: 2rem; }
  .highlight-text { font-size: 1rem; }
  .program-cta .btn { width: 100%; max-width: 280px; }
  .program-image-placeholder {
    width: 300px;
    height: 300px;
  }
  
  .stretching-icon svg {
    width: 80px;
    height: 80px;
  }
}

@media (max-width: 480px) {
  .program-highlights {
    padding: 3rem 0;
  }
  
  .program-content {
    gap: 2rem;
  }
  
  .program-title {
    font-size: 1.75rem;
  }
  
  .highlight-item {
    gap: 0.75rem;
    margin-bottom: 0.75rem;
  }
  
  .highlight-text {
    font-size: 0.95rem;
  }
  
  .program-image-placeholder {
    width: 250px;
    height: 250px;
  }
  
  .stretching-icon svg {
    width: 60px;
    height: 60px;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  .program-image-placeholder::before,
  .stretching-icon {
    animation: none;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .program-highlights {
    background-color: #fff;
  }
  
  .program-title {
    color: #000;
  }
  
  .highlight-text {
    color: #000;
  }
  
  .program-image-placeholder {
    background: #000;
  }
}
